import React from 'react';
import { color } from '../constants';
import styled from 'styled-components';

const nav = [
  {
    id: "#about",
    name: "Nosotros",
  },
  {
    id: "#service",
    name: "Servicios",
  },
  {
    id: "#content",
    name: "Contenido",
  },
  {
    id: "#contact",
    name: "Contacto",
  },
  {
    id: "#legal",
    name: "Legal",
  },
];

const Nav = styled.nav`
  display:flex;
  align-items: center;
`
const NavLink = styled.a`
  text-decoration: none;
  margin-left: 1rem;
  color: ${({ down }) => down ? color.light : color.phanton};
  transition: 250ms ease;
  &:visited{
    color: none;
  }
  &:hover{
    color: ${color.light};
    text-decoration: underline;
  }
`

export default ({ down })=> (
  <Nav>
    {console.log(down)}
    {
      nav.map(item => <NavLink down={down} key={item.id} href={item.id}>{item.name}</NavLink>)
    }
  </Nav>
)
